import { createSlice } from '@reduxjs/toolkit';

let member = createSlice({
    name: 'member',
    initialState:
        [
            { id: 'jjwwoo', password: 'ekfhd0415!', name: '장재우', age: 50 },
            { id: 'admin', password: 'admin' ,name: '관리자', age: 1 }
        ],
    reducers: {

        addMember(state, action) {
            state.push(action.payload)
        }
    }

})
export let { addMember } = member.actions

export default member