/* eslint-disable */
import { configureStore, createSlice } from '@reduxjs/toolkit'
import user from './store/userSlice.js';
import member from './store/memberSlice.js';

//store에서 공유 하는 값들은 전체 컴포넌트에서 사용할때만 사용
//일부 컴포넌트 개별 컴포넌트에서 사용하는 값들은 props 또는 context 사용


let cart = createSlice({
    name: 'cart',
    initialState:
        [
            { id: 0, name: 'White and Black', count: 2 },
            { id: 2, name: 'Grey Yordan', count: 1 }
        ],
    reducers: {

        addCount(state, action) {
            // let 찾은상품 = state.find(function(x){
            //     return x.id == action.payload
            //    });
            // 찾은상품.count += 1
            // 아래랑 같은 의미
            let 찾은상품 = state.findIndex((a) => { return a.id == action.payload })
            state[찾은상품].count++
        },
        addItem(state, action) {
            // return [...state, action.payload]
            // 아래랑 같은 의미
            let 찾은상품 = state.findIndex((a) => { return a.id == action.payload })
            console.log("찾은상품 ==> " + 찾은상품);
            
            state.push(action.payload)
        },
        subtractCount(state, action) {
            let 찾은상품 = state.findIndex((a) => { return a.id == action.payload })
            if (state[찾은상품].count > 0) {
            state[찾은상품].count--
            }
            else null;

        },

    }

})



export let { addCount, addItem, subtractCount } = cart.actions


export default configureStore({
    reducer: {
        user: user.reducer,
        // stock : stock.reducer,
        cart: cart.reducer,
        member: member.reducer
    }
}) 