import { useDispatch, useSelector } from 'react-redux';
import { changeName, increase } from './../store/userSlice.js';
import { addCount, subtractCount } from './../store.js';

//import { memo, useState } from 'react';

import { useNavigate } from 'react-router-dom';
import { useContext, useEffect, useState } from "react";
import { Button, Container, Nav, Navbar, Row, Col, Form, Modal } from 'react-bootstrap';


function Login() {

    let navigate = useNavigate();
    let [id, SetId] = useState('');
    let [password, SetPassword] = useState('');

    let state = useSelector((state) => { return state })


    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    const [show1, setShow1] = useState(false);
    const handleClose1 = () => setShow1(false);
    const handleShow1 = () => setShow1(true);




    return (
        <div className="margin-top-control">
            <div
                className="modal show"
                style={{ display: 'block', position: 'initial' }}
            >
                <Modal.Dialog>
                    <Modal.Header>
                        <Modal.Title>로그인 창</Modal.Title>
                    </Modal.Header>

                    <Modal.Body>
                        <Form>
                            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">

                                <Form.Control type="id" placeholder="id" onChange={(e) => {
                                    SetId(e.target.value)
                                }} />
                                <br />
                                <Form.Control type="password" placeholder="password" onChange={(e) => {
                                    SetPassword(e.target.value)
                                }} />
                            </Form.Group>
                        </Form>
                    </Modal.Body>

                    <Modal.Footer>

                        <Button variant="secondary" onClick={() => {

                            let 아이디찾기 = state.member.find(x => x.id == id);
                            //let 비밀번호저장 = 아이디찾기.password


                            if (아이디찾기 == null || 아이디찾기 == '') {
                                console.log("회원 가입 해주세요")
                                handleShow()
                            }

                            if ( 아이디찾기 != null && 아이디찾기 != '' && 아이디찾기.password == password ){
                                navigate('/'),
                                localStorage.setItem('member', 아이디찾기.name)
                                }
                            if ( 아이디찾기 != null && 아이디찾기 != '' && 아이디찾기.password != password){
                                console.log("비밀번호가 틀렸습니다.");
                                handleShow1()
                            }
                            
                                
                            

                                 // if (아이디찾기 == null || '') {
                            //     console.log("회원 가입 해주세요")
                            //     handleShow()
                            // }
                            // else {

                            //     if ( 비밀번호저장 == password ){
                            //         console.log("아이디 정보 : " + JSON.stringify(아이디찾기))
                            //         console.log("이름 정보 : " + JSON.stringify(아이디찾기.name))
                            //         navigate('/'),
                            //         localStorage.setItem('member', 아이디찾기.name)
                            //     }
                            //     else {
                            //         handleShow1()
                            //     }
                                
                            // }


                        }}>로그인</Button>

                        <Button onClick={() => { navigate('/addmember') }} variant="secondary">회원가입</Button>
                    </Modal.Footer>
                </Modal.Dialog>

                <Modal show={show} onHide={handleClose}>
                    <Modal.Header closeButton>
                        <Modal.Title>로그인을 할 수 없습니다.</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                    [ {id} ] 아이디는 회원가입 되어 있지 않습니다. <br/>
                    회원가입 진행 후 로그인 해주세요.</Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={handleClose}>
                            Close
                        </Button>
                    </Modal.Footer>
                </Modal>

                <Modal show={show1} onHide={handleClose1}>
                    <Modal.Header closeButton>
                        <Modal.Title>비밀번호 불일치</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                    [ {id} ] 의 비밀번호와 다릅니다. <br/>
                    올바른 비밀번호를 다시 입력 해주세요.</Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={handleClose1}>
                            Close
                        </Button>
                    </Modal.Footer>
                </Modal>

            </div>


        </div>
    )


}
export default Login