import { useDispatch, useSelector } from 'react-redux';
import { changeName, increase } from './../store/userSlice.js';
import { addCount, subtractCount } from './../store.js';
//import { Button } from 'react-bootstrap';
import { memo, useState } from 'react';
//import Modal from 'react-bootstrap/Modal';
//import Form from 'react-bootstrap/Form';
//import { addMember } from './../store.js';
import { Button, Container, Nav, Navbar, Row, Col, Form, Modal } from 'react-bootstrap';

import { addMember } from './../store/memberSlice.js';
import { useNavigate } from 'react-router-dom';



function Addmember() {

    let navigate = useNavigate();

    let [id, SetId] = useState('');
    let [password, SetPassword] = useState('');
    let [name, SetName] = useState('');
    let [age, SetAge] = useState('');

    let dispathch = useDispatch()

    //console.log("member는 : " + state.member[0].id);

    return (
        <div className="margin-top-control">
            <div
                className="modal show"
                style={{ display: 'block', position: 'initial' }}
            >
                <Modal.Dialog>
                    <Modal.Header>
                        <Modal.Title>회원가입 창</Modal.Title>
                    </Modal.Header>

                    <Modal.Body>
                        <Form>
                            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">

                                <Form.Control type="id" placeholder="id" onChange={(e)=> {
                                    SetId(e.target.value) 
                                }}/>
                                <br />
                                <Form.Control type="password" placeholder="password" onChange={(e)=> {
                                    SetPassword(e.target.value) 
                                }}/>
                                <br />
                                <Form.Control type="name" placeholder="name" onChange={(e)=> {
                                    SetName(e.target.value) 
                                }}/>
                                <br />
                                <Form.Control type="age" placeholder="age" onChange={(e)=> {
                                    SetAge(e.target.value) 
                                }}/>
                            </Form.Group>
                        </Form>
                    </Modal.Body>

                    <Modal.Footer>
                    <Nav.Link onClick={() => { navigate('/') }}>
                        <Button variant="secondary" onClick={() => {

                            dispathch(addMember({ id: id, password: password,name: name, age: age }))
                       
                        }}>등록하기</Button>
                        </Nav.Link>
                    </Modal.Footer>
                </Modal.Dialog>
               
            </div>
        </div>
    )


}
export default Addmember